import React from 'react'

const Tile2 = (props) => {
    
    const {type, categories, selected, className, full} = props;
    const {comments, votes, shared, levels} = props;


    const {post = {}} = props;
    const {title, text, img, videoId, by} = post;


    console.log('props', props);

    const getText = () => {
        console.log('getText', text);
        return text.split('##').map((item, key) => {
            return <span key={key}>{item}<br/></span>
          });

    }
    const getTypes = () => {

    }
    const getImg = () => {
        if (img) {
            return <img src={`/media/images/${img}`} width={`${full?480:300}px`} />
        }
    }

    const getVideo = () => {
        if (videoId) {

            let player;

            
            
            



         return (   
            <div>
            <iframe type="text/html" width={`${full?480:300}`} 
            src={`https://www.youtube.com/embed/${videoId}?enablejsapi=1`}
            frameBorder="0"
            id={`video${videoId}`}></iframe>

            <script>{`
                // create the global player from the specific iframe (#video)
                const player = new YT.Player('video${videoId}', {
                    events: {
                    // call this function when player is ready to use
                    'onReady': onPlayerReady
                    }
                });

                const playerEmbedded = document.getElementById("video${videoId}");
                console.log('playerEmbedded', playerEmbedded);
                
                function onPlayerReady(event) {
  
                    // bind events
                    const playerEmbedded = document.getElementById("video${videoId}");
                    playerEmbedded.addEventListener("mouseover", function() {
                      console.log('playerEmbedded', 'mouseover');
                        player.playVideo();
                    });
                    
                    playerEmbedded.addEventListener("mouseout", function() {
                        console.log('playerEmbedded', 'mouseout');
                      player.pauseVideo();
                    });
                    
                  }
            
            `}

            </script>
            </div>
);
        }
    }

    return (
        
        <div className={`cell size2 ${selected ? 'selected' : ''}  ${className || ''}`}>
            <div className="header">
                {getTypes()} {title}
            </div>

            
            <div className="center">
                <div className="img">
                    {getImg()}
                    {getVideo()}

                </div>

                
                <div className="content">
                    <div className="body">
                    {getText()}
                    </div>
                    <div className="footer">
                        
                        <div className="actions">
                            <div className="comments">comments</div> 
                            <div className="votes">votes</div> 
                            <div className="shared">shared</div>
                        </div>
                        <div className="score">
                            levels
                        </div>
                        
                    </div>
                </div>
            </div>



            <style jsx> 
        {`

        
        .center {
            display: flex;
            flex-direction: row;

        }
        .img {
            margin-top: 13px;
            width: 300px;
            opacity: 1;
        }

        .content {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 230px;
        }

        .cell:last-of-type {
            margin-right: 0px;
        }
        .row:last-of-type {
            margin-bottom: 0px;
        }
        .cell {
            background-color: #F4F6F6;
            border: 1px solid #F0F2F2;
            border-radius: 5px;
            margin-right: 11px;
            height: 282px;

            color: #707070;
            letter-spacing: 0;
        }

        .cell:hover, .selected {   
            background-color: #D3DDDD;
            border: 1px solid #4E7876;
            color: #000000;
            opacity: 1;
        }

        .cell:hover > .center > .img {
            opacity: 1;
        }

        .cell.size1 {
            width: 325px;
        }

        .cell.size2 {
            width: 663px;
        }

        .cell.size3 {
            width: 325px;
            height: 577px;
        }

        .cell.featured {
            border: 1px solid #C2CCCC;
        }
        .featured .header {
            border-bottom: 2px solid #D3DDDD;
        }

        .cell.available {
            border: 2px dashed #C2CCCC;
            background-color: #fff; 
            width: 323px;
            height: 280px;
        }
        
        .available .header {
            border-bottom: 2px dashed #D3DDDD;
        }

        .row {
            display: flex;
            margin-bottom: 11px;
        }
        
        .row.colspan { 
            flex-flow: wrap;
            width: 676px;
            margin-bottom: 0px !important;
        }

        .row.colspan .cell { 
            margin-bottom: 11px;
        }

        .last { 
            margin-bottom: 0px !important;
        }

        .header, .body, .footer, .img {
            padding: 0px 28px 0px 28px;
        }


        .body {
            margin-top: 13px;
            font: 12px/17px Arial;
        }

        .actions {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;
        }

        .footer {
            align-items: center;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }

        .header {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;

            height: 40px;
            border-bottom: 2px solid #FFFFFF;
            
            font: Bold 16px/19px Arial;
        }

        `}
            
        </style>
        
        </div>
    );

}

export default Tile2;